* {
  box-sizing: border-box;
}

body {
  color: #212121;
  background-color: #fdf6e2;
  margin: 0;
  line-height: 1.5;
}

.search-form input {
  height: 40px;
  min-width: 300px;
  border: none;
  border-radius: 4px;
  outline: none;
  margin: 0;
}

.search-form button {
  cursor: pointer;
  height: 40px;
  border: none;
  border-radius: 4px;
  margin: 0;
  position: relative;
  right: 10px;
}

.search-form button:hover {
  box-shadow: inset 0 1px 6px rgba(26, 26, 37, .5);
}

.header-styles {
  height: 100px;
  background-color: #335ce2;
  background-image: linear-gradient(to top, rgba(46, 47, 66, .7), rgba(82, 85, 141, .7));
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 1px 6px rgba(26, 26, 37, .5);
}



